.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.home__header {
  color: white;
  align-self: stretch;
}

.home__header h1 {
  font-style: normal;
  font-weight: normal;
  text-align: center;
  font-size: 24px;
  line-height: 28px;
}

.home__main {
  display: flex;
  flex-direction: column;
  align-content: center;

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  color: #adadad;
  text-align: center;
}

.home__header-message h1 {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.home__control {
  margin-bottom: 50px;
}

.home__control-button {
  display: flex;
  background: none;
  justify-content: center;
  cursor: pointer;
}

.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

.Panel__in {
  background: -webkit-gradient(linear, left top, left bottom, from(#2b00a5), to(#110041)), #ffffff;
  background: -webkit-linear-gradient(top, #2b00a5 0%, #110041 100%), #ffffff;
  background: linear-gradient(180deg, #2b00a5 0%, #110041 100%), #ffffff;
}

.PanelHeader__container,
.PanelHeader-right,
.PanelHeader-left-in,
.PanelHeader-content {
  background-color: #2b00a5;
}

.Group {
  margin: 10px 10px;
  border-radius: 5px;
}

.Group:before,
.Group:after {
  display: none;
}

